<template>
  <v-navigation-drawer
    id="core-navigation-drawer"
    v-model="drawer"
    :dark="barColor !== 'rgba(228, 226, 226, 1), rgba(255, 255, 255, 0.7)'"
    :expand-on-hover="expandOnHover"
    :right="$vuetify.rtl"
    :src="barImage"
    mobile-break-point="960"
    app
    width="260"
    v-bind="$attrs"
  >
    <template v-slot:img="props">
      <v-img :gradient="`to bottom, ${barColor}`"  :src="barImage" v-bind="props"/>
    </template>

    <v-divider class="mb-1" />

    <v-list
      dense
      nav
    >
      <v-list-item>
          <v-img src="@/assets/Icon.png"/>
      </v-list-item>
    </v-list>

    <v-divider class="mb-2" />

    <v-list expand nav>
      <!-- Style cascading bug  -->
      <!-- https://github.com/vuetifyjs/vuetify/pull/8574 -->
      <div />

      <template v-for="(item, i) in computedItems">
    
        <base-item-group
          v-if="item.children" :key="`group-${i}`" :item="item" >
          <!--  -->
        </base-item-group>

        <base-item v-else :key="`item-${i}`" :item="item" />
      </template>

      <!-- Style cascading bug  -->
      <!-- https://github.com/vuetifyjs/vuetify/pull/8574 -->
      <div />
    </v-list>
    
    <template v-slot:append>
      <div @click="logout()">
        <base-item :item="{ title: $t('log_out'),icon: 'el-icon-switch-button' }" />
      </div>

      <!-- <el-divider></el-divider> -->
      <div class="ml-5" style="color:#ffff; font-size:10px; text-align:left;"> <p> version du logiciel {{versionApp}} </p></div>
    </template>
  </v-navigation-drawer>
</template>

<script>

  // Utilities
  import { mapState,} from 'vuex';
  import NProgress from 'nprogress';
  import 'nprogress/nprogress.css';
  import axios from 'axios';
  import package_json from "../../../../../package.json";


  export default {
    name: 'X-Performance',

    props: {
      expandOnHover: {
        type: Boolean,
        default: false,
      },
    },

    data: () => ({
      items: [
        {
          icon: 'el-icon-menu',
          title: 'Acceuil',
          to: '/',
        },
        
      ],

      versionApp : '',
      res_menu : [],
    }),

    computed: {
      ...mapState(['barColor', 'barImage']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          this.$store.commit('SET_DRAWER', val)
        },
      },
      computedItems () {
        return this.items.map(this.mapItem)
      },
      profile () {
        return {
          avatar: true,
          title: this.$t('avatar'),
        }
      },
    },

    methods: {

      mapItem (item) {

        return {
          ...item,
          children: item.children ? item.children.map(this.mapItem) : '',
          title: this.$t(item.title),
        }
      },


      logout() {
          NProgress.start();
          this.$auth.logout({
                makeRequest: true,
                redirect: {name: 'login'},
          });
          NProgress.done();
      },

      getMenuSection() {
        
        // Récupération des acces pour le menu
        NProgress.start();
        try {

          const response =  JSON.parse(localStorage.getItem('menu'));
          var menu = response.menu.menu;

          menu.forEach(element => {
            this.res_menu.push(element);
          });

            this.buildMenu();
        } 
        catch (error) {
            this.$notify({
                type: 'error',
                title: "Echec de recuperation"
            });
        }

      },

      // Fonction pour la creation d'un menu dynamique
      buildMenu() {
        this.res_menu.forEach(element => {

          if (element == 'report') {
              var el = {
                icon: 'el-icon-s-order',
                title: 'Rapport',
                to: '/pages/rapport',
              };

              this.items.push(el);
          }
          else if(element == 'statistique') {

              var el = {
                icon: 'el-icon-data-analysis',
                title: 'Statistique',
                to: '/pages/statistique',
              };

              this.items.push(el);

          }
          else if(element == 'administration') {

              var el = {
                icon: 'el-icon-s-cooperation',
                title: 'Administration',
                to: '/pages/administration',
              };

              this.items.push(el);

          }
          else if(element == 'contact') {

              var el = {
                icon: 'el-icon-s-custom',
                title: 'Client',
                to: '/pages/client',
              };

              this.items.push(el);

          }
          else if(element == 'setting') {

              var el = {
                icon: 'el-icon-setting',
                title: 'Paramètre',
                to: '/pages/parametre',
              };

              this.items.push(el);
          }
          
        });
        var el1 = {
                icon: 'el-icon-chat-line-square',
                title: 'Messagerie',
                to: '/pages/message',
              };
        this.items.push(el1);
        
        var el = {
                icon: 'el-icon-user',
                title: 'Mon compte',
                to: '/pages/profile',
              };
              
        this.items.push(el);

        NProgress.done();
      }
    },
    created() {
      this.getMenuSection();
      this.versionApp = package_json.version;
    },
    mounted() {
    },
  
  }
</script>

<style lang="sass">
  @import '~vuetify/src/styles/tools/_rtl.sass'

  #core-navigation-drawer
    .v-list-group__header.v-list-item--active:before
      opacity: .24

    .v-list-item
      &__icon--text,
      &__icon:first-child
        justify-content: center
        text-align: center
        width: 20px

        +ltr()
          margin-right: 24px
          margin-left: 12px !important

        +rtl()
          margin-left: 24px
          margin-right: 12px !important

    .v-list--dense
      .v-list-item
        &__icon--text,
        &__icon:first-child
          margin-top: 10px

    .v-list-group--sub-group
      .v-list-item
        +ltr()
          padding-left: 8px

        +rtl()
          padding-right: 8px

      .v-list-group__header
        +ltr()
          padding-right: 0

        +rtl()
          padding-right: 0

        .v-list-item__icon--text
          margin-top: 19px
          order: 0

        .v-list-group__header__prepend-icon
          order: 2

          +ltr()
            margin-right: 8px

          +rtl()
            margin-left: 8px
</style>
